// import share from '@/components/share';
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
import {
	mapGetters,
	mapMutations
} from "vuex"
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			indicatorDots: false,
			isDisabled: false,
			sumPrice: 0, //合计价格
			sum: 0, //合计购买数量
			selected: 0, //选中项
			scrollLeft: 0, //swiper离左边距离
			goodData: {}, //商品详情数据
			specClass: 'none',
			carNum: 0, //加入购物车后的数量
			// specSelected: [],
			favorite: true, //收藏
			shareList: [],
			flagPage1: false, //完成按钮详情页面跳转
			falgPage: 0,
			specList: [], //多规格商品列表
			singleSpecList: {}, //单规格
			sleectSpecList: [], //选中的多规格列表
			userInfo: [], // 用户信息（包含token）
			shareStr: '', // 用户分享码
			id: '' // 商品id
		};
	},
	computed: {
		...mapGetters({
			getshopNum: 'getshopNum'
		})
	},
	onShow() {
		this.userInfo = uni.getStorageSync('user_info')

		// uni.showToast({
		// 	title: '还未登录，请先登录',
		// 	duration: 1000,
		// 	icon: 'none'
		// })
		// setTimeout(res => {
		// 	uni.switchTab({
		// 		url: '/pages/mine/mine'
		// 	})
		// }, 1000)
	},
	async onLoad(options) {
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		this.id = options.id;

		if (this.id) {
			// console.log(id);
			this.getData(this.id);
		}
		// 获取购物车数据
		this.getCartData();

		// 如果是通过分享进入，获取参数并保存
		// console.log(options.s)
		if (options.s) {
			uni.setStorageSync('shareStr', options.s)
		} else {
			uni.setStorageSync('shareStr', '')
		}
		console.log(uni.getStorageSync('shareStr'))
		// this.carNum = this.getshopNum;
		// this.shareList = await this.$api.json('shareList');
	},
	methods: {
		...mapMutations({
			setShopNum: 'setShopNum',
			setOrderData:'setOrderData',
			setOrderState:'setOrderState'
		}),
		// 获取购物车数据
		getCartData() {
			this.$api.carList().then(res => {
				console.log(res)
				if (res.data.errno == 0) {
					console.log(res.data.data.count)
					this.setShopNum(Number(res.data.data.count))
				}else{
					this.setShopNum(0)
				}
			})
		},
		// 规格选择
		selectSpecifications() {
			this.flagPage1 = false;
			this.toggleSpec();
		},
		// 获取商品详情数据
		getData(id) {
			this.$api.goods({
				goods_id: id
			}).then(res => {
				console.log(res);
				if (res.data.errno == 0) {
					this.goodData = res.data.data;
					if (this.goodData.standards == 2) {
						this.specList = res.data.data.price_data;
						this.specList.map((item, index) => {
							item.buyNum = 0;
							item.content.map((val, kay) => {
								val.num = 0;
							})
						})
					} else {
						console.log(res.data.data.price_data);
						this.singleSpecList = res.data.data.price_data;
						this.singleSpecList.retail_price = Number(this.singleSpecList.retail_price);
						this.singleSpecList.trade_price = Number(this.singleSpecList.trade_price);
						this.singleSpecList.num = 0;
					}

				} else {
					console.log("获取失败")
				}
			})
		},
		clickTap(index) {
			this.selected = index;
			this.scrollLeft = (index - 1) * 100;
		},

		// 立即购买接口
		settementCart(data) {
			this.$api.orederSettlement({
				commodity: data
			}).then(res => {
				if (res.data.errno == 0) {
					console.log(res);
					this.setOrderState(0);//将页面订单状态设置为未生成订单
					this.setOrderData(res.data.data); //将订单信息存入vuex中
					uni.navigateTo({
						url: '/pages/index/confirmOrder/confirmOrder'
					})
				}
			})
		},

		// 加入购物车
		complete() {
			let userInfo = uni.getStorageSync('user_info');
			console.log(userInfo);
			if (userInfo.token) {
				if (this.isDisabled == false) {
					return
				}
				// 判断完成按钮是否跳转确认订单页面
				console.log(this.flagPage1);
				if (this.flagPage1 == true) {
					// 跳转立即下单页面
					// 判断是多规格还是单规格
					let data = [];
					if (this.goodData.standards == 1) {
						// 单规格数据
						data.push({
							goods_id: this.singleSpecList.goods_id,
							num: this.singleSpecList.num,
							standard_id: this.singleSpecList.id
						})
					} else {
						// 多规格数据
						this.sleectSpecList.map((item, index) => {
							data.push({
								goods_id: item.goods_id,
								num: item.num,
								standard_id: item.id
							})
						})
					}
					// 调用结算接口
					this.settementCart(data);
				} else {
					let data = {
						goods_id: this.goodData.goods_id,
						params: []
					}
					if (this.goodData.standards == 1) {
						data.params.push({
							standard_id: this.singleSpecList.id,
							number: this.singleSpecList.num
						})
					} else {
						this.sleectSpecList.map((item, index) => {
							data.params.push({
								standard_id: item.id,
								number: item.num
							})
						})
					}
					this.$api.cartAdd(data).then(res => {
						console.log(res);
						if (res.data.errno == 0) {
							this.isDisabled = false;
							uni.showToast({
								title: '已成功加入购物车',
								duration: 1000,
								icon: "none"
							});
							// 加入购物车后清空购物车
							// this.carNum += this.sum;
							// 更新vuex中购物车数量
							// this.setShopNum(this.carNum);
							this.getCartData()
							this.clearCart();
							if (this.falgPage != 1) {
								this.toggleSpec();
							}

						}else if(res.data.errno === 400 || res.data.errno === 4008 || res.data.errno === 4009){
							uni.showToast({
								title: '登录已过时,请重新登录',
								duration: 1000,
								icon: 'none'
							})
							
							uni.setStorageSync('user_info', {
								token: '',
								user_id: '',
								user_img: '../../static/img/mine/default-img.png',
								user_name: '点击头像登录'
							});
							
							setTimeout(() => {
								uni.navigateTo({
									url: '/pages/mine/mine'
								})
							},1000)
						}
					})
				}
			}else{
				uni.showToast({
					title: '还未登录',
					duration: 1000,
					icon: 'none'
				})
				
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				})
			}
		},
		// 清空购物车
		clearCart() {
			console.log(this.specList);
			this.specList.map((item, index) => {
				item.buyNum = 0;
				item.content.map((val, key) => {
					val.num = 0
				})
			})
			this.sumPrice = 0, //合计价格
				this.sum = 0, //合计购买数量
				this.sleectSpecList = [];
			this.singleSpecList.num = 0;
		},
		//多规格 
		change(e) {
			console.log(e);
			if (e.num == "") {
				e.num = 0
			}
			this.$set(this.specList[e.index1].content[e.index2], "num", Number(e.num));
			let sum = 0;
			this.specList[e.index1].content.map((val, i) => {
				sum += val.num
			})
			this.$set(this.specList[e.index1], "buyNum", sum);
			// 合计台数
			this.totalAmount();
			// 判断按钮状态
			if (this.sum >= this.goodData.start_quantity) {
				this.isDisabled = true;
			} else {
				this.isDisabled = false;
			}
			// 筛选加购的数据
			this.filterData();
		},
		filterData() {
			this.sleectSpecList = [];
			this.specList.map((item, index) => {
				item.content.map((val, key) => {
					if (val.num > 0) {
						let data = val;
						data.model = item.title;
						this.sleectSpecList.push(data);
					}
				})
			})
		},
		// 单规格
		singleChange(e) {
			console.log(e);
			this.singleSpecList.num = Number(e.num);
			console.log(typeof this.singleSpecList.num)
			this.sum = this.singleSpecList.num;
			this.sumPrice = 0;
			if (this.singleSpecList.num >= this.singleSpecList.wholesale_quantity) {
				console.log("我是批发价")
				this.sumPrice = Number(this.sumPrice) + this.singleSpecList.num * this.singleSpecList.trade_price.toFixed(2)
			} else {
				this.sumPrice = Number(this.sumPrice) + this.singleSpecList.num * this.singleSpecList.retail_price.toFixed(2)
			}
			this.sumPrice = this.sumPrice.toFixed(2);
			// 判断按钮状态
			if (this.sum >= this.goodData.start_quantity) {
				this.isDisabled = true;
			} else {
				this.isDisabled = false;
			}
		},
		toShopCart() {
			uni.switchTab({
				url: "/pages/shopCart/shopCart"
			})
		},
		// 加入购物车
		addCart() {
			if (this.userInfo.token) {
				this.flagPage1 = false;
				// if (this.sleectSpecList.length > 0) {
				if (this.isDisabled == false) {
					uni.showToast({
						title: '商品起购数量为' + this.goodData.start_quantity,
						duration: 1000,
						icon: "none"
					});
					this.toggleSpec();
				} else {
					this.falgPage = 1;
					this.complete()
				}
				// } else {
				// 	this.toggleSpec();
				// }
			} else {
				uni.showToast({
					title: '还未登录，请先登录',
					duration: 1000,
					icon: 'none'
				})
				setTimeout(res => {
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				}, 1000)
			}

		},
		// 跳转到购物车页面
		toShopCar() {
			// 判断是否登录，如果没有登录，去到个人中心让用户登录
			if (this.userInfo.token) {
				uni.switchTab({
					url: '/pages/shopCart/shopCart'
				})
			} else {
				uni.showToast({
					title: '还未登录，请先登录',
					duration: 1000,
					icon: 'none'
				})
				setTimeout(res => {
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				}, 1000)
			}
		},
		// 跳转店铺主页
		toStoreHome() {
			console.log(this.goodData)
			uni.setStorageSync('factory_id', this.goodData.factory_id)
			uni.navigateTo({
				url: "/pages/index/storeHome/storeHome"
			})
		},
		// 跳转确认订单页面
		toConfirmOrder() {
			// 判断是否登录
			if (this.userInfo.token) {
				// 判断
				// if (this.sleectSpecList.length > 0 || this.) {
				this.flagPage1 = true;
				// 判断数量是否大于或者等于起购数量
				if (this.sum >= this.goodData.start_quantity) {
					console.log("我是结算结算结算！！！！！")
					 // 判断是多规格还是单规格
					 let data = [];
					 if (this.goodData.standards == 1) {
					 	// 单规格数据
					 	data.push({
					 		goods_id: this.singleSpecList.goods_id,
					 		num: this.singleSpecList.num,
					 		standard_id: this.singleSpecList.id
					 	})
					 } else {
					 	// 多规格数据
					 	this.sleectSpecList.map((item, index) => {
					 		data.push({
					 			goods_id: item.goods_id,
					 			num: item.num,
					 			standard_id: item.id
					 		})
					 	})
					 }
					 // 调用结算接口
					this.settementCart(data);
				} else {
					uni.showToast({
						title: '商品起购数量为' + this.goodData.start_quantity,
						duration: 1000,
						icon: "none"
					});
					this.toggleSpec();
				}
				// } else {
				// 	this.toggleSpec();
				// 	// 标识完成按钮跳转立即购买页面
				// 	// this.falgPage = 1;
				// }
			} else {
				uni.showToast({
					title: '还未登录，请先登录',
					duration: 1000,
					icon: 'none'
				})
				setTimeout(res => {
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				}, 1000)
			}
		},
		// 合计数量和价格
		totalAmount() {
			this.sum = 0;
			this.sumPrice = 0;
			this.specList.map((item, inde) => {
				this.sum += item.buyNum;
				item.content.map((val, i) => {
					console.log(val.wholesale_quantity);
					if (val.num >= val.wholesale_quantity) {
						this.sumPrice += val.num * Number(val.trade_price).toFixed(2)
					} else {
						this.sumPrice += val.num * Number(val.retail_price).toFixed(2)
					}
				})
			})
			this.sumPrice = this.sumPrice.toFixed(2)
		},
		changeTab(e) {
			this.selected = e.detail.current;
			this.scrollLeft = (e.detail.current - 1) * 100;
		},
		//规格弹窗开关
		toggleSpec() {
			this.falgPage = 0;
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		//分享
		share() {
			this.$refs.share.toggleMask();
		},
		//收藏
		toFavorite() {
			this.favorite = !this.favorite;
		},
		stopPrevent() {},

		// 分享按钮点击
		shareFn() {
			uni.showToast({
				title: '还未登录，请先登录',
				duration: 1000,
				icon: 'none'
			})
			setTimeout(res => {
				uni.switchTab({
					url: '/pages/mine/mine'
				})
			}, 1000)
			return false;
		},
		// 获取分享码
		getShareCode() {
			this.$api.shareCode().then(res => {
				if (res.errno == 0) {
					this.shareStr = res.data.data
				}
			})
		}
	},
	onShareAppMessage(options) {
		let that = this;
		let shareObj = {
			title: that.goodData.name,
			path: '/pages/index/shopDetail/shopDetail?s=' + that.shareStr + '&id=' + this.id,
		}

		if (options.from == 'button') {
			shareObj.path = '/pages/index/shopDetail/shopDetail?s=' + that.shareStr + '&id=' + this.id;
		}
		return shareObj;

	},
}
